.form {
  list-style: none;
  padding: 0 0 24px;
  margin: 0;
  display: grid;
  grid-template-columns: 238px;
  grid-template-rows: repeat(2, max-content);
  row-gap: 30px;

  
}

@media screen and (min-width: 768px) {
  .form {
    padding: 0 0 36px;
    grid-template-columns: 358px;
  }
}